import { Component, OnInit, HostListener } from '@angular/core'
import { environment } from '../environments/environment'
import { PaymentService } from "./payments/payment.service"
import { ExchangeRatePipe } from "./pipes/exchange-rate.pipe"
import { ExchangeRateService } from "./exchange-rate.service"
import { Rate } from "./payments/payment.model"

import { FirebaseListObservable, FirebaseObjectObservable } from "angularfire2/database"
import { Observable } from "rxjs/Observable";

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css'],
    providers: [ PaymentService ]
})
export class AppComponent implements OnInit {
    title = 'Kutsh'
    exchangeRate: number
    amount:number
    amountReceived:number
    totalAmount:number
    fee:number
    maximumTransferable:number = 70000
    handler:any
    
    fc:string[] = ['usd', 'gbp', 'eur']
    lc:string[] = ['kes', 'tzs', 'ugx']
    
    theRate:any = {}

    selectedFc:string = 'usd'
    selectedLc:string = 'kes'

    constructor(
        private paymentService:PaymentService,
        private exchangeRateService: ExchangeRateService) {}

    ngOnInit() {
        this.exchangeRateService.currencies().subscribe( c => this.theRate = c)
    }

    calcFee():number {
        this.er()
        this.fee = this.amount >= 100 ? 2.80 : 1.40
        this.totalAmount = this.fee + this.amount
        this.amountReceived = this.amount ? this.exchangeRate * this.amount : this.exchangeRate
        return this.fee
    }

    er() {
        // this.exchangeRateService
        //     .getExchangeRate(this.selectedFc, this.selectedLc)
        //     .subscribe(res => this.exchangeRate = res[this.selectedLc])  
        
        for (const k in this.theRate) {
            if (this.theRate.hasOwnProperty(k) && k == this.selectedFc) {
                this.exchangeRate = this.theRate[k][this.selectedLc]
            }
        }
    }

    ngOnChanges(){}

    handlePayment():void {

        this.handler = StripeCheckout.configure({
            key: environment.stripeKey,
            image: '/assets/images/kutsh-logo.jpg',
            locale: 'auto',
            token: token => {
                this.paymentService.processPayment(token, this.amount)
            }
        })

        
        this.handler.open({
            name: 'Kutsh',
            description: 'Transfer Funds to MPesa',
            zipCode: true,
            currency: 'gbp',
            // billingAddress: true,
            amount: this.totalAmount * 100
        })
    }

    @HostListener('window:popstate')
    onPopstate() {
        this.handler.close()
    }
}



