import { Injectable } from '@angular/core'
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from "angularfire2/database"
import { Rate } from "./payments/payment.model"

import { Observable } from "rxjs/Observable"
import 'rxjs/add/operator/map'
import 'rxjs/add/observable/of'
import 'rxjs/add/operator/take'

@Injectable()
export class ExchangeRateService {
    constructor(private db:AngularFireDatabase) { }

    currencies(): FirebaseObjectObservable<any> {
        return this.db.object('/currencies')
    }

    getExchangeRate(fc:string, lc:string): FirebaseObjectObservable<any> {
        return this.db.object(`currencies/${fc}`)
    }
}



